@import url(https://fonts.googleapis.com/css2?family=Bodoni+Moda:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Jost:wght@400;600&display=swap);
a {
  text-decoration: none;
  color: black;
}

body {
  margin: 0;
  font-family: 'Jost', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.page-container {
  margin-top: 80px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.page-container h1 {
  color: rgb(65, 96, 153);
}

.important-button {
  font-size: 18px;
  font-family: inherit;
  text-decoration: none;
  border-radius: 3px;
  background-color: rgb(21, 36, 100);
  padding: 10px 20px;
  color: white;
  margin: 20px 0; 
  border: none;
}

.important-button:hover {
  background-color: rgb(77, 85, 119);
}
#home-page-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: calc(100vh - 70px);
  margin-top: 70px;
}

#home-page-header {
  margin: 0;
  text-align: center;
  font-family: 'Bodoni Moda';
  font-size: 5vmax;
  padding: 0 1vmax;
  background: linear-gradient(45deg, rgb(13,25,79), rgb(89, 114, 177));
  -webkit-background-clip: text;
          background-clip: text;
  -webkit-text-fill-color: transparent;
}

#home-page-icons {
  text-align: center;
  font-size: 4vmax;
  margin: 15px 0;
}

#home-page-icons span {
  margin: 0 1vmax;
}

#home-page-description {
  width: 45vmax;
  max-width: 80vw;
  background-color: rgb(245, 245, 245);
  padding: 10px 20px;
  border-radius: 3px;
}

#home-page-description p {
  font-size: 18px;
}

.red {
  color: rgb(206, 29, 29);
}
.store-page-item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 280px;
  border-radius: 10px;
  cursor: pointer;
}

.store-page-item:hover {
  font-weight: 600;
  letter-spacing: 1.5px;
}

.item-title {
  font-size: 17px;
  margin-bottom: -15px;
  transition: letter-spacing 0.2s;
  color: rgb(59, 59, 59);
}

.item-image-container {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 90%;
  overflow: hidden;
}

.item-image-container img {
  max-width: 100%;
  max-height: 100%;
  z-index: -1;
  opacity: 1;
  transition: transform 0.2s;
}

.item-image-container:hover img {
  transform: scale(1.125);
  animation: fade-in 0.2s linear;
  animation-fill-mode: forwards;
}

@keyframes fade-in {
  0% {
    opacity: 0.3;
  }

  100% {
    opacity: 1;
  }
}
#store-page-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  grid-gap: 20px;
  gap: 20px;
}

@media only screen and (min-width: 600px) {
  #store-page-grid {
    max-width: 70vw;
  }
}

@media only screen and (min-width: 1400px) {
  #store-page-grid {
    max-width: 50vw;
    grid-template-columns: repeat(4, minmax(155px, 1fr));
  }
}

@media only screen and (max-width: 599px) {
  #store-page-grid {
    grid-template-columns: repeat(2, minmax(155px, 1fr));
    max-width: 100vw;
  }
}
.image-carousel {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 500px;
  width: 400px;
}

.carousel-selection {
  position: relative;
  top: 60px;
}

.carousel-selection p {
  text-align: center;
}

.carousel-selection button {
  margin: 0 8px;
  background-color: rgb(231, 231, 241);
  border: none;
  border-radius: 3px;
  font-size: 16px;
  padding: 3px 8px;
  color: rgb(38, 49, 78);
  font-family: inherit;
}

.carousel-selection button:hover {
  background-color: rgb(197, 197, 212);
}

.image-carousel img {
  height: 100%;
  cursor: pointer;
}

.carousel-selection-indicator {
  position: relative;
  bottom: 40px;
  display: inline-block;
  background-color: rgb(231, 231, 241);
  color: transparent;
  padding: 0;
  width: 12px;
  height: 12px;
  margin: 0 8px;
  border: solid 2px transparent;
  border-radius: 50%;
  cursor: pointer;
}

.carousel-selection-indicator:focus {
  outline: none;
  border-color: rgb(38, 49, 78);
}

.carousel-selected {
  background-color: rgb(126, 126, 155) !important;
}
#item-page-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 80px;
}

#item-text {
  max-width: 50vw;
  margin: 0 20px;
}

#item-text h1 {
  margin: 0;
}

@media only screen and (max-width: 650px) {
  #item-page-container {
    flex-direction: column;
  }

  #item-text {
    max-width: 80vw;
  }
}

#item-price {
  margin: 8px 0;
  font-size: 20px;
  font-weight: bold;
  color: rgb(89, 129, 202);
}

#item-quantity-label {
  color: gray;
  font-size: 16px;
  padding-right: 15px;
  border-right: solid 1px rgb(187, 187, 187);
}

#item-quantity-input {
  color: rgb(102, 102, 102);
  font-size: 20px;
  width: 4ch;
  font-family: inherit;
  padding: 4px 10px;
  padding-left: 10px;
  margin: 0 15px;
  border: solid 1px rgb(185, 185, 185);
  border-radius: 3px;
}

#add-to-cart-button {
  background-color: rgb(89, 129, 202);
  color: white;
  font-size: 20px;
  border: none;
  border-radius: 3px;
  padding: 5px 10px;
  font-family: inherit;
  cursor: pointer;
  margin: 5px 0;
}

#added-to-cart-message {
  margin-left: 10px;
  color: rgb(89, 129, 202);
  animation: fade-out 2s cubic-bezier(.99,.02,.87,.6);
  animation-fill-mode: forwards;
}

@keyframes fade-out {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
.cart-order-container {
  display: flex;
  flex-direction: column;
}

.cart-order-info p {
  margin: 2px;
}

.cart-order  {
  display: flex;
  align-items: center;
}

.cart-order img {
  width: 85px;
  max-width: 15vw;
  height: auto;
}

.cart-order-name {
  font-weight: bold;
}

.cart-order-quantity {
  margin-left: 10px;
  font-size: 16px;
  font-family: monospace;
}

.change-order-quantity-button {
  background-color: white;
  border: none;
  font-size: 17px;
  color: rgb(180, 198, 221);
  cursor: pointer;
}

.change-order-quantity-button:hover {
  color: rgb(73, 93, 129);
}

.change-order-quantity-button:focus {
  outline: none;
  color:rgb(47, 60, 83);
}
#navbar {
  position: fixed;
  top: 0;
  background: rgb(37,65,133);
  background: linear-gradient(90deg, rgba(37,65,133,1) 0%, rgba(13,25,79,1) 100%);
  display: flex;
  align-items: center;
  width: 100%;
  height: 70px;
  padding: 5px 10px;
  box-sizing: border-box;
  box-shadow: rgb(37,65,133, 0.2) 0px 1px 5px 3px;
}

.navbar-item {
  -webkit-text-decoration-line: none;
          text-decoration-line: none;
  color: white;
  margin: 0 20px;
  
  color: rgb(255, 255, 255, 0.55);
}

.navbar-item.active {
  color: rgba(255, 255, 255);
  font-weight: 600;
}

@media only screen and (max-width: 320px) {
  .navbar-item {
    margin: 0 10px;
  }
}

.navbar-item:hover {
  color: rgb(255, 255, 255, 0.3);
}

#navbar-title {
  position: relative;
  bottom: 3px;
  font-size: 36px;
  font-family: 'Bodoni Moda';
  color: white;
}

#basket-link {
  margin-left: auto;
}

#cart-item-count {
  margin-left: 10px;
}
